import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        "users":
            [
                {"username":"bradley","pass":"asdfgh","locked":false},
                {"username":"sasha","pass":"qwerty","locked":false},
                {"username":"maya","pass":"zxcvbn","locked":false}
            ],
        "new_users": [],
    },
    getters: {
        users: state => state.users,
        new_users: state => state.new_users,
    },
    mutations: {
        ADD_User(state, user_email) {
            state.new_users.push(user_email)
        }
    },
    actions: {
        addUser(context, email) {
            context.commit("ADD_User", email)
        }
    },
    modules: {}
})