<template>
  <div id="app">
    <img alt="CIT logo" src="./assets/citlogo.png">
    <div v-show="form_appear">
      <div id="buttons">
        <button  class="form-trigger-buttons" @click.prevent="showSignUp">Click to Sign-Up</button>
        <button  class="form-trigger-buttons" @click.prevent="showLogin">Click to Login</button>
      </div>
      <div class="container-fluid h-100">
        <LogInForm @inputData="updateName" v-show="loginreveal"></LogInForm>
        <SignUpForm @inputData="updateName" v-show="signupreveal"></SignUpForm>
      </div>

    </div>
    <PlayernameBanner :playername="childData" v-show="appearance"></PlayernameBanner>
    <Gameplay v-show="appearance" @quit="getStatus"></Gameplay>
  </div>
</template>

<script>
import LogInForm from "@/components/LogInForm";
import SignUpForm from "./components/SignUpForm";
import PlayernameBanner from "@/components/PlayernameBanner";
import Gameplay from "@/components/Gameplay";
export default {
  name: 'App',
  components: {
    SignUpForm,
    LogInForm,
    PlayernameBanner,
    Gameplay
  },
  data: function () {
    return {
      childData: "",
      has_quit:false,
      appearance: false,
      signupreveal:false,
      loginreveal:false,
      form_appear:true
    };
  },
  methods: {
    updateName(variable) {
      this.childData = variable;
      if (this.childData !== ""){
        this.appearance = true;
        // this.signupreveal = false;
        // this.loginreveal = false;
        this.form_appear = false;
      }
    },
    showSignUp: function (){
      this.signupreveal = true;
      this.loginreveal = false;
    },
    showLogin: function (){
      this.signupreveal = false;
      this.loginreveal = true;
    },
    getStatus: function (status) {
      this.has_quit = status
      if (this.has_quit){
        this.appearance = false;
        this.form_appear = true;
      }
    }
  }
}
</script>

