<template>
  <div class="rounded px-5 py-4 shadow bg-white text-left">
    <div class="d-flex justify-content-center align-items-center">
      <form v-show="!loggedIn">
        <div class="errors">
          <span v-show="invalid">Invalid Credentials. </span>
          <span v-show="locked_account">This account has been locked. </span>
        </div>
        <div class="row">
          <div class="col-12 form-group">
            <label class="col-form-label col-form-label-lg">NickName<span class="text-danger"></span></label>
            <input type="text" class="form-control form-control-lg" v-model="username">
          </div>
          <div class="col-12 form-group">
            <label class="col-form-label col-form-label-lg">Password(Minimum of 6 characters)<span class="text-danger"></span></label>
            <input type="password" class="form-control form-control-lg" v-model="pass">
          </div>
          <div class="col-12 form-group text-center">
            <button class="btn btn-primary mb-2" @click.prevent="on_login()">Login</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
// var credentials =
//     [
//         {username:"bradley",pass:"asdfgh",locked:false},
//         {username:"sasha",pass:"qwerty",locked:false},
//         {username:"maya",pass:"zxcvbn",locked:false}
//     ]
export default {
name: "LogInForm",
  data: function (){
    return{
      username:"",
      pass:"",
      cred:[],
      disable_btn: true,
      loggedIn: false,
      login_counter: 0,
      token: null,
      locked_account: false,
      invalid: false
    }
  },
  computed: {
    existing_users(){
      return this.$store.getters.users
    },
    new_users(){
      return this.$store.getters.new_users
    }
  },
  methods: {
    on_login: function (){
      this.cred = this.existing_users.concat(this.new_users)
      var i
      var found = false
      for (i=0; i<this.cred.length;i++){
        if (this.username.toString()===this.cred[i].username) {
          this.token = this.cred[i]
          if (this.pass.toString() === this.cred[i].pass) {
            found = true
          }
        }
      }
      if (found) {
        this.invalid = false
        if (this.token.locked){
          this.username = ""
          this.pass = ""
        }else{
          this.locked_account = false
          this.invalid = false
          this.$emit("inputData", this.username);
          this.username = ""
          this.pass = ""
        }

      }else{
        this.locked_account = false
        this.invalid = true
        this.username = ""
        this.pass = ""
        this.login_counter++
        this.check_attempts()
      }
    },
    check_attempts: function (){
      if (this.login_counter===3){
        this.token.locked = true
        this.locked_account = true
      }
    }
  }
}
</script>

<style scoped>
  .errors{
    color: #f44336;
  }
</style>