<template>
  <div>
    <h2>Player vs Computer</h2>
    <h2>{{ userScore }} - {{ compScore }}</h2>
    <div v-show="!end">
      <div class="button_options">
        <button class="options" @click="select('rock')">
          <img class="options_pic" src="../assets/rock.jpg" alt="rock">
        </button>
        <button class="options" @click="select('paper')">
          <img class="options_pic" src="../assets/paper.jpg" alt="paper">
        </button>
        <button class="options" @click="select('scissors')">
          <img class="options_pic" src="../assets/scissors.jpg" alt="scissors">
        </button>
      </div>
      <div class="visual_cues" v-show="!end">
        <h2>Player Chooses: {{userChoice}}</h2>
        <h2>Computer Chooses: {{compChoice}}</h2>
        <h2 v-show="!tie">Winner/Result of this round: {{roundWinner}}</h2>
        <h2 v-show="tie">It's a tie!</h2>
      </div>
    </div>
    <h1 v-show="end">{{winner}} is the winner!</h1>
    <button class="form-trigger-buttons" @click="restart">Restart</button>
    <button class="form-trigger-buttons" @click="quit">Quit</button>
  </div>
</template>

<script>

export default {
name: "Gameplay",
  data: function () {
    return {
      userChoice : null,
      compChoice : null,
      userScore : 0,
      compScore : 0,
      maxScore : 10,
      winner: null,
      roundWinner: null,
      tie: false,
      end: false,
    };
  },
  methods: {
    select : function (choice) {

      this.userChoice = choice;
      const choices = ['rock', 'paper', 'scissors'];
      this.compChoice = choices[Math.floor(Math.random() * choices.length)];
      this.setScore()
    },
    restart : function() {
      this.userChoice = null
      this.compChoice = null
      this.userScore = 0
      this.compScore = 0
      this.maxScore = 10
      this.roundWinner = null
      this.winner = null
      this.tie = false
      this.end = false
    },
    setScore : function () {

      if (this.userChoice===this.compChoice){
        this.tie = true
      }else{
        this.tie = false
        if (this.userChoice==='rock'){
          if (this.compChoice==='scissors'){
            this.userScore++
            this.roundWinner = "Player"
          }else if (this.compChoice==='paper'){
            this.compScore++
            this.roundWinner = "Computer"
          }
        }else if (this.userChoice==='paper'){
          if (this.compChoice==='rock'){
            this.userScore++
            this.roundWinner = "Player"
          }else if (this.compChoice==='scissors'){
            this.compScore++
            this.roundWinner = "Computer"
          }
        }else if (this.userChoice==='scissors'){
          if (this.compChoice==='paper'){
            this.userScore++
            this.roundWinner = "Player"
          }else if (this.compChoice==='rock'){
            this.compScore++
            this.roundWinner = "Computer"
          }
        }
        this.check_score()
      }
    },
    check_score: function(){
      if (this.compScore===this.maxScore){
        if (this.compScore>this.userScore) {
          this.winner = "Computer"
        }
        this.end = true
      }else if (this.userScore===this.maxScore){
        if (this.userScore>this.compScore){
          this.winner = "Player"
        }
        this.end = true
      }
    },
    quit : function (){
      this.userScore = 0
      this.compScore = 0
      this.winner = null
      this.end = false
      this.$emit("quit", true);
    }
  }
}
</script>

<style scoped>
.options:hover {
  background-color: #f44336;
}
@media screen and (max-width: 800px) {
  h1,h2{
    font-size: x-large;
  }
  .options {
    max-width: 30%;
    height: auto;
  }
  .options_pic {
    max-width: 100%;
    height: auto;
  }
}
</style>