<template>
    <div class="rounded px-5 py-4 shadow bg-white text-left">
      <div class="d-flex justify-content-center align-items-center">
        <form>
          <div class="row">
            <div class="col-12 form-group">
              <label class="col-form-label col-form-label-lg">NickName<span class="text-danger"></span></label>
              <input type="text" class="form-control form-control-lg" v-model="nickname">
            </div>
            <div class="col-12 form-group">
              <label class="col-form-label col-form-label-lg">Email<span class="text-danger"></span></label>
              <input type="text" class="form-control form-control-lg" v-model="email">
            </div>
            <div class="col-12 form-group">
              <label class="col-form-label col-form-label-lg">Password(Minimum of 6 characters)<span class="text-danger"></span></label>
              <input type="password" class="form-control form-control-lg" v-model="password1">
            </div>
            <div class="col-12 form-group">
              <label class="col-form-label col-form-label-lg">Repeat-Password<span class="text-danger"></span></label>
              <input type="password" class="form-control form-control-lg" v-model="password2">
              <span v-show="msg2">{{ pwd2_msg }}</span>
            </div>
            <div class="col-12 form-group text-center">
              <button class="btn btn-primary mb-2" :disabled="disable_btn" @click.prevent="on_signup()">Sign-Up</button>
            </div>
          </div>
        </form>
      </div>
    </div>

</template>

<script>
export default {
  name: "SignUpForm",
  data: function() {
    return {
      nickname:'',
      email: '',
      email_msg: '',
      password1: '',
      pwd1_msg: '',
      password2: '',
      pwd2_msg: '',
      disable_btn: true,
      msg1: true,
      msg2: true,
      appear:false,
      formappear:true
    }
  },
  watch: {
    email: function(value) {
      this.valid_email(value, 'email_msg');
    },
    password1: function(value) {
      if(this.check_password_length(value, 'pwd1_msg', 6)) {
        this.check_passwords_match();
      }
    },
    password2: function(value) {
      if(this.check_password_length(value, 'pwd2_msg', 6)) {
        this.check_passwords_match();
      }
    }
  },
  methods:{
    valid_email(email, msg) {
      if (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
        this[msg] = '';
        return true;
      } else {
        this[msg] = 'Keep typing...waiting for a valid email';
        return false;
      }
    },
    check_password_length(value, msg, total) {
      var length = value.length;
      var sum = 0;
      var display;

      sum = (total - length);

      switch(sum) {
        case 0:
          display = '';
          break;
        case 1:
          display = 'Keep going - just need '+ sum + ' more character.';
          break;
        default:
          display = 'Keep going - just need '+ sum + ' more characters';
      }

      if(length >= total) {
        this[msg] = '';
        return true;
      } else {
        this[msg] = display;
        return false;
      }

    },
    check_passwords_match() {
      if(this.password1.length > 5 && this.password2.length > 5) {
        if(this.password2 !== this.password1) {
          this.pwd2_msg = 'Passwords do not match';
          this.disable_btn = true;
          return true;
        } else {
          this.pwd2_msg = '';
          this.disable_btn = false;
          return false;
        }
      }
    },
    on_signup() {
      var data = {
        "username":this.nickname,
        "pass":this.password2,
        "locked":false
      }
      this.$store.dispatch("addUser", data)
      this.nickname = '';
      this.email = '';
      this.password1 = '';
      this.password2 = '';
      this.email_msg = '';
      this.pwd1_msg = '';
      this.pwd2_msg = '';
      this.msg1 = false;
      this.msg2 = false;
      this.disable_btn = true;
      this.$emit("inputData", this.nickname);
      this.appear = true;
    }
  }
}
</script>

